.breadcrumbs {
  &__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    color: #ff6600;
    position: relative;
    margin-bottom: 5px;
    &:not(:last-child) {
      margin-right: 20px;
     &:after {
       content: '';
       position: absolute;
       top: 50%;
       right: -17px;
       transform: translate(0, -50%);
       width: 14px;
       height: 14px;
       background-image: url("../img/arrow.png");
       background-repeat: no-repeat;
       background-position: center;
       background-size: 14px 14px;
     }
    }
    &--active {
      font-size: 14px;
      line-height: 120%;
      color: #222222;
    }
  }
}