.plywood {
  padding: 50px 0;
  &__inner {
    margin-top: 40px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #aaabaa;
  }
  &__title {
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
    color: #222222;
  }
  &__social {
    display: flex;
    align-items: center;
    margin-left: 25px;
    &-icon {
      width: 26px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      color: #3f3f3f;
      margin-left: 7px;
      a {
        color: #3bc915;
      }
    }
  }
  &__content {
    display: flex;
    margin-top: 40px;
  }
  &__left {
    display: flex;
    width: calc(100% - 357px);
  }
  &__right {
    width: 307px;
    margin-left: 50px;
  }
  &__box {
    width: 443px;
    margin-right: 45px;
  }
  &__desc {
    width: calc(100% - 488px);
  }
  &__box-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  &__promo {
    display: flex;
    align-items: center;
  }
  &__action {
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
    height: 34px;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eb1a1f;
  }
  &__sale {
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
    height: 34px;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #25344c;
    margin-left: 7px;
  }
  &__size {
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
    height: 34px;
    padding: 0 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ff6600;
    margin-left: 10px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 17px;
    line-height: 120%;
    font-weight: 700;
    color: #222222;
    margin-bottom: 15px;
  }
  &__text {
    font-size: 15px;
    line-height: 140%;
    color: #222222;
    margin-bottom: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 0;
  }
  &__item {
    height: 45px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    padding: 0 15px;
    &:nth-child(odd) {
      border: 1px solid #ececec;
      background: #fcfcfc;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      color: #222222;
    }
    &-info {
      font-size: 15px;
      line-height: 120%;
      color: #222222;
    }
  }
  &__link {
    display: inline-block;
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #ff6600;
    padding-bottom: 5px;
    margin-top: 15px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: #ff6600;
    }
  }
  &__right-inner {
    border: 1px solid #ececec;
    box-shadow: -16px 9px 20px 0px rgba(0,0,0,0.02);
    padding: 0 25px;
  }
  &__price {
    width: calc(100% + 50px);
    margin-left: -25px;
    background: #000000;
    padding: 20px 25px;
    &-title {
      font-size: 15px;
      line-height: 105%;
      font-weight: 600;
      color: #ffffff;
    }
    &-desc {
      font-size: 26px;
      line-height: 105%;
      font-weight: 700;
      text-shadow: -3px 2px 6px rgba(0,0,0,0.12);
      color: #ffffff;
      margin-top: 10px;
      span {
        font-size: 19px;
      }
    }
  }
  &__status {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #aaabaa;
    padding: 20px 0;
    &-icon {
      width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 15px;
      line-height: 120%;
      font-weight: 600;
      color: #222222;
      margin-left: 7px;
    }
  }
  &__fav-btns {
    border-bottom: 1px solid #aaabaa;
    padding: 20px 0;
  }
  &__fav {
    font-size: 13px;
    line-height: 105%;
    color: #aaabaa;
    padding-left: 22px;
    background-image: url("../img/favorites2.png");
    background-repeat: no-repeat;
    background-position: left 0 top 50%;
    background-size: 15px auto;
    cursor: pointer;
    &-hide {
      display: none;
      color: #f60;
    }
    &:hover {
      background-image: url("../img/favorites.png");
    }
    &--active {
      background-image: url("../img/favorites.png");
      .plywood__fav-show {
        display: none;
      }
      .plywood__fav-hide {
        display: block;
      }
    }
  }
  &__comp {
    font-size: 13px;
    line-height: 105%;
    color: #aaabaa;
    padding-left: 22px;
    background-image: url("../img/favorites2.png");
    background-repeat: no-repeat;
    background-position: left 0 top 50%;
    background-size: 15px auto;
    cursor: pointer;
    margin-top: 15px;
    &-hide {
      display: none;
      color: #f60;
    }
    &:hover {
      background-image: url("../img/favorites.png");
    }
    &--active {
      background-image: url("../img/favorites.png");
      .plywood__comp-show {
        display: none;
      }
      .plywood__comp-hide {
        display: block;
      }
    }
  }
  &__info {
    display: flex;
    padding: 20px 0 0 0;
    &-desc {
      font-size: 13px;
      line-height: 140%;
      color: #222222;
      margin-left: 7px;
    }
  }
  &__tel {
    display: block;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 10px;
    padding-left: 22px;
    padding-bottom: 20px;
    border-bottom: 1px solid #aaabaa;
  }
  &__btn {
    width: calc(100% + 50px);
    margin-left: -25px;
    padding: 20px 25px;
    background: #fcfcfc;
   a {
     width: 196px;
     height: 46px;
     display: flex;
     justify-content: center;
     align-items: center;
     background: #f60;
     border-radius: 4px;
     font-size: 15px;
     line-height: 105%;
     font-weight: 700;
     color: #ffffff;
   }
  }
  &__download {
    flex-shrink: 0;
    width: 301px;
    height: 41px;
    border: 1px solid #ff6600;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-left: 20px;
    margin-bottom: 10px;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      text-transform: uppercase;
      color: #ff6600;
      margin-left: 7px;
    }
  }
  &__tabs {
    margin-top: 60px;
    &-head {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-btns {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
    &-body {
      margin-top: 50px;
    }
    &-content {
      display: none;
      &--active {
        display: block;
      }
    }
  }
  &__tab {
    height: 44px;
    padding: 0 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #cecece;
    border-radius: 4px;
    font-size: 16px;
    line-height: 120%;
    cursor: pointer;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 20px;
    }
    &:hover {
      background-color: #25344c;
      color: #ffffff;
    }
    &--active {
      background-color: #25344c;
      color: #ffffff;
    }
  }
  &-payment {
    &__title {
      color: #323232;
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
    }
    &__items {
      display: grid;
      grid-template-columns: 1fr;
      gap: 20px;
      margin-top: 20px;
    }
    &__item {
      display: flex;
      align-items: center;
    }
    &__item-icon {
      flex-shrink: 0;
      width: 74px;
      height: 74px;
      border-radius: 74px;
      border: 2px dashed #ff6600;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      img {
        position: relative;
        z-index: 2;
      }
      &:after {
        position: absolute;
        display: block;
        content: "";
        z-index: 1;
        width: 58px;
        height: 58px;
        background: #ff6600;
        border-radius: 58px;
        top: 50%;
        left: 50%;
        margin-top: -29px;
        margin-left: -29px;
      }
    }
    &__item-desc {
      margin-left: 12px;
    }
    &__item-title {
      color: #404040;
      font-size: 17px;
      line-height: 120%;
      font-weight: 600;
    }
    &__item-text {
      padding-top: 8px;
      color: #404040;
      font-size: 15px;
      line-height: 140%;
    }
  }
  &-desc {
    &__title {
      color: #323232;
      font-size: 16px;
      line-height: 120%;
    }
    &__info {
      background: #fcfcfc;
      border: #edeeee 1px solid;
      border-left: #ff6600 4px solid;
      border-radius: 5px;
      padding: 24px;
      min-height: 88px;
      margin-top: 15px;
    }
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px 15px;
      margin-top: 30px;
    }
    &__item {
      display: flex;
    }
    &__item-icon {
      flex-shrink: 0;
      width: 52px;
      height: 52px;
      background-color: #ff6600;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: auto;
      }
    }
    &__item-desc {
      margin-left: 17px;
    }
    &__item-text {
      font-size: 14px;
      color: #222222;
      line-height: 140%;
    }
  }
  &-characteristic {
    &__items {
      display: grid;
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__item {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px dotted #000;
    }
    &__item-title {
      color: #323232;
      font-size: 16px;
      line-height: 120%;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1px;
        width: calc(100% + 5px);
        height: 2px;
        background: #ffffff;
      }
    }
    &__item-info {
      color: #323232;
      font-size: 16px;
      line-height: 120%;
      margin-left: 15px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: -1px;
        width: calc(100% + 5px);
        height: 2px;
        background: #ffffff;
      }
    }
  }
  &-delivery {
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px 15px;
    }
    &__item {
      display: flex;
      align-items: center;
    }
    &__item-icon {
      flex-shrink: 0;
      width: 74px;
      height: 74px;
      border-radius: 74px;
      border: 2px dashed #ff6600;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      img {
        position: relative;
        z-index: 2;
      }
      &:after {
        position: absolute;
        display: block;
        content: "";
        z-index: 1;
        width: 58px;
        height: 58px;
        background: #ff6600;
        border-radius: 58px;
        top: 50%;
        left: 50%;
        margin-top: -29px;
        margin-left: -29px;
      }
    }
    &__item-desc {
      margin-left: 12px;
    }
    &__item-title {
      color: #404040;
      font-size: 17px;
      line-height: 120%;
      font-weight: 600;
    }
    &__list {
      margin-top: 40px;
      li {
        font-size: 16px;
        line-height: 140%;
        color: #323232;
        padding-left: 20px;
        position: relative;
        &:not(:first-child) {
          margin-top: 10px;
        }
        &:after {
          position: absolute;
          display: block;
          content: "";
          width: 6px;
          height: 6px;
          left: 0;
          top: 8px;
          background: #ff6600;
          border-radius: 50%;
        }
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .plywood {
    &__right {
      width: 250px;
      margin-left: 30px;
    }
    &__left {
      width: calc(100% - 280px);
    }
    &__box {
      width: 400px;
      margin-right: 30px;
    }
    &__desc {
      width: calc(100% - 430px);
    }
    &__right-inner {
      padding: 0 15px;
    }
    &__price {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 20px 15px;
    }
    &__btn {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 20px 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .plywood {
    &__inner {
      margin-top: 10px;
    }
    &__top {
      display: block;
    }
    &__title {
      font-size: 32px;
    }
    &__social {
      margin: 15px 0 0 0;
    }
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
    &__tabs-head {
      flex-direction: column;
      align-items: start;
    }
    &__download {
      order: -1;
      margin: 0 0 20px 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .plywood {
    &__left {
      display: block;
    }
    &__desc {
      width: 100%;
      margin: 15px 0 0 0;
    }
    &-desc {
      &__items {
        grid-template-columns: 1fr;
        gap: 20px;
      }
    }
    &-delivery {
      &__items {
        grid-template-columns: 1fr;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .plywood {
    &__title {
      font-size: 28px;
    }
    &__box {
      width: 100%;
    }
    &__price {
      &-desc {
        font-size: 22px;
        span {
          font-size: 16px;
        }
      }
    }
    &__box-top {
      display: block;
    }
    &__size {
      display: inline-flex;
      margin: 10px 0 0 0;
    }
    &__tabs-btns {
      display: grid;
      grid-template-columns: 1fr;
      gap: 10px;
      width: 100%;
    }
    &__tab {
      margin: 0;
      &:not(:last-child) {
        margin: 0;
      }
    }
    &__download {
      width: 100%;
    }
  }
}